import { get_info } from "./class"

const get_video_info = async(id: string, resolution: string): Promise<get_info> => {

    const url: string = `https://ebd.cda.pl/620x395/${id}?wersja=${resolution}p`
    const f: Response = await fetch(url, {
        method: 'GET',
        headers: {
            "User-Agent": 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/88.0.4324.150 Safari/537.36'
        },
        cf: {
            cacheTtl: 14000,
            cacheEverything: true,
            cacheKey: id,
        }
    })
    const media_object: get_info = new get_info()
    await new HTMLRewriter()
        .on(`#mediaplayer${id}`, media_object) //Get direct url for current quality
        .transform(f)
        .text()

    return media_object
}

export {get_video_info}