// CDA
API.add('GET', `/${API_VERSION_V1}/json/:id/:res`, async (req, res) => {
    const id: string = req.params.id
    const resolution: string = req.params.res

    const video_info: object = await get_video_info(id, resolution)
    res.send(200, video_info)
});

API.add('GET', `/${API_VERSION_V1}/player/:id/:res`, async (req, res) => {
    const id: string = req.params.id
    const resolution: string = req.params.res
    const video_info: object = await get_video_info(id, resolution)
    const html: string = `
    <html>
        <head>
            <style>
                body {
                    margin: 0 auto;
                    background: rgb(18, 18, 18) !important;
                }
                video {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    bottom: 0px;
                    left: 0px;
                    max-height: 100%;
                    max-width: 100%;
                    margin: auto;
                }
                </style>
        </head>
        <body>
            <video id="player" playsinline controls data-poster="${video_info["thumb"]}">
                <source src="${video_info["url"]}" type="video/mp4" />
            </video>
        </body>
    </html>
    `
    res.setHeader("Content-Type", "text/html")
    res.send(200, html)
});

listen(API.run)
