import { decode } from './decode';

class get_info {
    title: string;
    id: string;
    duration: string;
    thumb: string;
    url: string;

    element(element){
        const data: JSON = JSON.parse(element.getAttribute('player_data'))

        this.title = decodeURI(data['video']['title'])
        this.id = data['video']['id']
        this.duration = data['video']['duration']
        this.thumb = data['video']['thumb']
        this.url = decode(data['video']['file'])
    }
}

export {get_info}